<script>
export default {
  name: 'addDoc',
  data() {
    return {
      comName: '',
      imageUrl: '',
      hosName: [
        {value: '01', label: 'xxx-p医院'},
        {value: '02', label: '---xx医院'},
        {value: '03', label: '其他'}
      ],
      hosType: [
        {value: '01', label: '公立医院'},
        {value: '02', label: '私立医院'},
        {value: '03', label: '其他'}
      ],
      level1: [],
      level2: [],
      docLevel: [],
      hosLeve: [
        {value: '01', label: '三级甲等'},
        {value: '02', label: '二级乙等'},
        {value: '03', label: '其他'}
      ],
      provList: [],
      cityList: [],
      areaList: [],
      value: '',
      textarea: '',
      radio: ''
    }
  },
  methods: {
     handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
      }
  }
}
</script>
 
<style lang="less" scoped>
.box{
